import React from 'react'
import SideDrawer from '../SideDrawer/SideDrawer'
import DrawerToggle from '../SideDrawer/DrawerToggle/DrawerToggle'
import {NavLink} from 'react-router-dom';
import classes from './MobileBar.module.css'

const toolbar = (props) => {

    const sideDrawer = props.drawerOpen ? 
    <React.Fragment>
        <SideDrawer open={props.drawerOpen} closeDrawer={props.closeDrawer}>
            <div className={props.type === 'top' ? [classes.ToolBar, classes.ToolBarTop].join(" ") : classes.ToolBar}>
                <div><NavLink to='/'>HOME</NavLink></div>
                <div><NavLink to='/projects'>PROJECT SAMPLES</NavLink></div>
                <div><NavLink to='/about'>ABOUT ME</NavLink></div>
                <div><NavLink to='/contact'>CONTACT</NavLink></div>
            </div>
        </SideDrawer>
        <div className={classes.BackDrop} onClick={props.toggleClicked}></div>
    </React.Fragment> : null;

    return(
        <React.Fragment>
            <header className={classes.Toolbar}>
                <DrawerToggle clicked={props.toggleClicked}/>
            </header>
            {sideDrawer}
        </React.Fragment>
        
    )
}

export default toolbar;