import React from 'react'
import classes from './background.module.css'
import topo1 from '../../images/Topo1.png'
import topo2 from '../../images/Topo2.png'
import topo3 from '../../images/Topo3.png'
import ToolBar from '../Navigation/Toolbar/Toolbar'
import Footer from '../footer/footer'

const background = (props) => {
        return (
            <div className={classes.Container}>
                {props.homeEl}
                <ToolBar type={props.type}/>
                <div className={props.type == 'home' ? classes.Home : classes.Content}>
                    <div className={classes.ContentRel}>
                        {props.children}
                    </div>
                    <Footer />
                </div>
                <div>
                    <img className={[classes.Topo1, classes.Topo].join(" ")} src={topo1} alt='topo1' />
                </div>
                <div>
                    <img className={[classes.Topo2, classes.Topo].join(" ")} src={topo2} alt='topo2' />
                </div>
                <div>
                    <img className={[classes.Topo3, classes.Topo].join(" ")} src={topo3} alt='topo3' />
                </div>
            </div>
    ) 
}

export default background;