import React, { Component } from 'react';
import classes from './about_me_page.module.css';
import Background from '../UI/background/background';
import ProfilePic from '../images/profile_pic.jpg';
import ToolBar from '../UI/Navigation/Toolbar/Toolbar';
import Footer from '../UI/footer/footer';

class AboutMe extends Component {
    componentDidMount() {
        window.scrollTo(0, 0)
    }

    render() {
        return (
            <div>
                <Background type='top'>
                    <div className={classes.Header}>
                        <h1>ABOUT ME</h1>
                        <img className={classes.Pic} src={ProfilePic} alt='profile_pic' />
                    </div>
                    <div className={classes.Main}>
                        <div className={classes.Item}>
                            <h2>Bio</h2>
                            <div className={classes.Body}>
                                <p>
                                    I am an up and coming software engineer with a passion for building high quality software. 
                                    I have a drive to learn and expand my knowledge while offering my skills developed through hours of hard work on professional and personal projects.
                                    Through university course work, professional expirence, and other projects I have learned the principles of writing clean, robust, and easily maintainable code.
                                    My personal interests include running, playing the piano, and spending time with my wife.
                                    I truly consider software development a hobby and along with finance and business.
                                </p>
                            </div>
                        </div>
                        
                        <div className={classes.Item}>
                            <h2>Education</h2>
                            <div className={classes.Body}>
                                <div className={classes.SubHeader}>
                                    <h3>Utah State University</h3>
                                    <h3 style={{"color": "#ffc7a1"}}>Graduating August 2021</h3>
                                </div>
                                <p>B.S., Computer Science, 3.95 GPA</p>
                                <div className={classes.Info}>
                                    <li>
                                        Learned principles of Data Structures and Algorithms and their applications.
                                    </li>
                                    <li>
                                        Gained skills in teamwork, problem solving, full stack web development, mobile application
                                        development, operating system design, databases, cloud computing, data science, artificial intelligence,
                                        and other topics.
                                    </li>
                                    <li>
                                        Completed a minor in business where skills in marketing, accounting, MIS, finance, and management
                                        were developed.
                                    </li>
                                </div>
                                
                            </div>
                        </div>

                        <div className={classes.Item}>
                            <h2>Expirence</h2>
                            <div className={classes.Body}>
                                <div className={classes.SubHeader}>
                                    <h3>Space Dynamics Laboratory</h3>
                                    <h3 style={{"color": "#ffc7a1"}}>Logan, UT</h3>
                                </div>
                                <div className={classes.SubHeader}>
                                    <p>Assistant Software Developer</p>
                                    <p style={{"color": "#ffc7a1"}}>August 2020 - Present</p>
                                </div>
                                <div className={classes.Info}>
                                    <li>
                                        Design, develop, evaluate, and maintain automated tests with Selenium, C#, and C++.
                                    </li>
                                    <li>
                                        Work on a team of developers and participate in agile software development.
                                    </li>
                                    <li>
                                        Perform code reviews and give feedback on proposed changes to code and design.
                                    </li>
                                    <li>
                                        Communicate bugs and issues through video calling, messaging, and in person discussion.
                                    </li>
                                    <li>
                                        Present solutions to software challenges with visuals and technical documents.
                                    </li>
                                </div>
                                <div className={classes.SubHeader} style={{"marginTop": 50}}>
                                    <p>Assistant Software Tester</p>
                                    <p style={{"color": "#ffc7a1"}}>November 2019 - August 2020</p>
                                </div>
                                <div className={classes.Info}>
                                    <li>
                                        Perform regression testing by interacting with the software, following technical procedures, and tests.
                                    </li>
                                    <li>
                                        Interact with MySQL databases using MySQL workbench and the CLI client on Linux. 
                                    </li>
                                    <li>
                                        Test new features and report results to developers and mangers.
                                    </li>
                                    <li>
                                        Report bugs found by documenting the issue and contacting appropriate parties.
                                    </li>
                                    <li>
                                        Use network design to interact with software over distributed systems and networks.
                                    </li>
                                    <li>
                                        Install and test software using the Unix CLI on RedHat and CentOS Linux systems.
                                    </li>
                                </div>
                            </div>
                        </div>
                        <div className={classes.Item}>
                            <h2>Skills</h2>
                            <div className={classes.Skills}>
                                <h3>Java</h3>
                                <h3>Python</h3>
                                <h3>C#</h3>
                                <h3>C++</h3>
                                <h3>JavaScript</h3>
                                <h3>React.js</h3>
                                <h3>Unix</h3>
                                <h3>AWS</h3>
                                <h3>Data Science</h3>
                                <h3>Relational Databases</h3>
                                <h3>Google Firebase</h3>
                            
                                <h3>Android Dev</h3>
                                <h3>Selenium</h3>
                                <h3>Git</h3>
                                <h3>Confluence</h3>
                                <h3>Jira</h3>
                                <h3>Nginx</h3>
                                <h3>Django</h3>
                                <h3>MVC</h3>
                                <h3>MVVM</h3>
                                <h3>Docker</h3>
                                <h3>Adobe XD</h3>
                            </div>
                            
                        </div>
                        <div className={classes.Item} style={{"marginBottom": 0}}>
                            <h2>Awards/Honors</h2>
                            <div className={classes.Body}>
                                <div className={classes.SubHeader}>
                                    <h3>King Scholarship Recipiant</h3>
                                    <h3 style={{"color": "#ffc7a1"}}>September 2020</h3>
                                </div>
                                <div className={classes.Info}>
                                    <li>
                                        Awarded by the Space Dynamics Laboratory, given to students with exceptional work performance and leadership.
                                    </li>
                                </div>
                                <div className={classes.SubHeader}>
                                    <h3>National AP Scholar Award</h3>
                                    <h3 style={{"color": "#ffc7a1"}}>May 2016</h3>
                                </div>
                                <div className={classes.Info}>
                                    <li>
                                        Awarded to high school students that have achieved a score of 4 or above on eight or more AP Exams.
                                    </li>
                                </div>
                            </div>
                        </div>
                    </div>
                </Background>   
            </div>
        )
    }
}

export default AboutMe;
