import React, { Component } from 'react';
import { Switch , Route } from 'react-router-dom'
import classes from './App.module.css';
import Layout from './UI/Layout/Layout'
import Home from './home_page/home_page'
import AboutMe from './about_me_page/about_me_page'
import Contact from './contact_page/contact_page'
import ProjectSamples from './projects_page/projects_page'

class App extends Component {
  
  render () {
    return (
      <div className={classes.App}>
        <Layout>
          <Switch>
            <Route path='/about' component={AboutMe} />
            <Route path='/contact' component={Contact} />
            <Route path='/projects' component={ProjectSamples} />
            <Route path='/' component={Home} />
          </Switch>
        </Layout>
      </div>
    );
  }

}

export default App;

