import React, { Component } from 'react';
import classes from './contact_page.module.css'
import Background from '../UI/background/background'
import axios from '../axios-main';
import ContactForm from './contact_page_form/contact_page_form'
import Spinner from '../UI/Spinner/Spinner'

class Contact extends Component {
    state = {
        spinner : false,
        message_sent: false,
        formConfigs: [
            {
                elementType: 'input',
                elementConfig: {
                    type: 'text',
                    placeholder: 'YOUR NAME'
                },
                value: "",
                red: false
            },
            {
                elementType: 'input',
                elementConfig: {
                    type: 'email',
                    placeholder: 'YOUR EMAIL'
                },
                value: "",
                red: false
            },
            {
                elementType: 'input',
                elementConfig: {
                    type: 'text',
                    placeholder: 'SUBJECT'
                },
                value: "",
                red: false
            },
            {
                elementType: 'textarea',
                elementConfig: {
                    type: 'text',
                    placeholder: 'MESSAGE'
                },
                value: "",
                red: false
            },
        ]
    }

    formElementChangedHandler = (event, idx) => {
        const formConfigs = [...this.state.formConfigs]
        formConfigs[idx].value = event.target.value;
        formConfigs[idx].red = false;
        this.setState({formConfigs: formConfigs})
    }

    componentDidMount() {
        window.scrollTo(0, 0)
    }

    scanAndHighlight = () => {
        this.state.formConfigs.forEach((el, idx) => {
            if (el.value === "") {
                const formConfigs = [...this.state.formConfigs]
                formConfigs[idx].red = true;
                this.setState({formConfigs: formConfigs})
            }
        })
    }

    sendButtonClickedHandler = () => {
        if (this.state.formConfigs[0].value === "" | 
            this.state.formConfigs[1].value === "" | 
            this.state.formConfigs[2].value === "" |
            this.state.formConfigs[3].value === "") {
            this.scanAndHighlight();
        } else {
            this.setState({spinner: true})
            axios.get(
                "/SendEmail?name=" + this.state.formConfigs[0].value +
                "&email=" + this.state.formConfigs[1].value +
                "&subject=" + this.state.formConfigs[2].value +
                "&message=" + this.state.formConfigs[3].value)
                .then(response => {

                    if (response.data['status'] === 'success') {
                        this.setState({spinner: false, message_sent: true})
                    } else {
                        alert("There was an error. Please try again later.")
                        this.setState({spinner: false})
                    }
                })
        }
    }

    render() {
        var onPage = 
            <React.Fragment>
                <p>
                    If you're interested in talking to me about my work and would like to connect please send me a message!
                </p>
                <div className={classes.Form}>
                    <div className={classes.FormContents}>
                        <ContactForm
                            buttonClickedHandler={this.sendButtonClickedHandler}
                            formElements={this.state.formConfigs}
                            changed={this.formElementChangedHandler}
                            buttonClass={classes.Button}/>
                    </div>
                </div>
            </React.Fragment>

        if (this.state.spinner) {
            onPage = <Spinner />
        }

        if (this.state.message_sent) {
            onPage = 
                <div style={{"height": "150px"}}>
                    <h2>Sent Successfully!</h2>
                </div>
        }

        return(
            <div>
                <Background type={'top'}>
                    <h1>CONTACT</h1>
                    {onPage}
                    <div className={classes.ContactInfo}>
                        <div className={classes.ContactInfoContents}>
                            <h2><a href="tel: 801-828-6960">(801) 828-6960</a></h2>
                            <h2><a href="mailto: dallinlarsen.professional@gmail.com">DALLINLARSEN.PROFESSIONAL@GMAIL.COM</a></h2>
                            {/*<h2><a href="https://github.com/dallinlarsen">GIT HUB</a></h2>*/}
                            <h2><a href="https://www.linkedin.com/in/dallinjlarsen/">LINKEDIN</a></h2>
                        </div>
                    </div>
                </Background>
            </div>
        )
    }
}

export default Contact;