import React, { Component } from 'react';
import classes from './projects_page.module.css';
import Background from '../UI/background/background';
import Analysis from '../images/Analysis.PNG';
import Budgets from '../images/Budgets.PNG';
import Category from '../images/Category.PNG';
import Database from '../images/Database.PNG';

class ProjectsPage extends Component {
    state = {
        selected: ""
    }

    constructor(props) {
        super(props)
        this.myRef = React.createRef();
    }

    componentDidMount() {
        window.scrollTo(0, 0)
    }

    componentDidUpdate() {
        this.scrollToElement();
    }

    scrollToElement = () => {
        if (this.myRef.current) {
            this.myRef.current.scrollIntoView();
        }
    }

    updateSelected = (value) => {
        if (this.state.selected !== value) {
            this.setState({selected: value})
        } else {
            this.setState({selected: ""})
        }
        
    }

    render() {
        return(
            <div>
                <Background type='top'>
                    <h1 ref={this.state.selected === "" ?  this.myRef : null}>PROJECTS</h1>
                    <div className={classes.Main}>
                    <div ref={this.state.selected === "Current" ?  this.myRef : null} className={classes.Item}>
                            <h2 onClick={() => this.updateSelected("Current")}>This Website</h2>
                            <div className={this.state.selected === "Current" ? null : classes.Hide}>
                                <div style={{"marginLeft": "30px"}}>
                                    <li>Developed using ReactJS on the frontend and Django on the backend.</li>
                                    <li>This is being hosted on a Ubuntu Linux AWS EC2 instance.</li>
                                    <li>The server is being ran using Nginx.</li>
                                    <li>It supports both desktop and mobile viewing.</li>
                                    <li>Is using the HTTPS protocol to ensure secure browsing.</li>
                                </div>
                            </div>
                        </div>
                        <div ref={this.state.selected === "Budget" ?  this.myRef : null} className={classes.Item}>
                            <h2 onClick={() => this.updateSelected("Budget")}>Budget Management Application</h2>
                            <div className={this.state.selected === "Budget" ? null : classes.Hide}>
                                <div style={{"marginLeft": "30px"}}>
                                    <li>Developed a website using ReactJS on the frontend and Django on the backend that manages budgets.</li>
                                    <li>Uses the MVC design that allows an API I developed to interact with an SQLite database on the backend.</li>
                                    <li>Integrates with the Plaid API, a service that automates information transfer from bank accounts.</li>
                                    <li>Supports both desktop and mobile viewing. This website also supports desktop and mobile viewing.</li>
                                    <li>Is hosted on a Rasberry Pi in my apartment using Nginx and Uwsgi to deploy the Django backend.</li>
                                </div>
                            <h3>Screen Shots</h3>
                            <div className={classes.PicBox}>
                                <img className={classes.PicSmall} src={Budgets} alt='analysis' />
                                <img className={classes.PicSmall} src={Database} alt='analysis' />
                                <img className={classes.PicBig} src={Analysis} alt='analysis' />
                                <img className={classes.PicBig} src={Category} alt='analysis' />
                            </div>
                            </div>
                        </div>
                        <div ref={this.state.selected === "Stocks" ?  this.myRef : null} className={classes.Item}>
                            <h2 onClick={() => this.updateSelected("Stocks")}>Stock Trading Algorithm</h2>
                            <div className={this.state.selected === "Stocks" ? null : classes.Hide}>
                                <div style={{"marginLeft": "30px"}}>
                                    <li>Built a stock trading framework that streams stock data and executes stock trading strategies.</li>
                                    <li>Features a highly adaptable framework to manage multiple strategies and time periods.</li>
                                    <li>Runs each trading day off a Rasberry Pi in my apartment. It uses chrontab to intiate the program and shuts down if it finds it is not a trading day.</li>
                                    <li>Written using Python and Pandas, integrated into the Polygon and Alpaca APIs.</li>
                                    <li>Features a strategy derived from training linear regression models using Sci-Kit Learn.</li>
                                    <li>As part of the data stream from polygon, it uses a proxy server deployed using a Docker Container.</li>
                                    <li>Uses databases to extract data for backtesting that were set up by converting json data responses to pandas dataframes which were then stored into SQLite3 databases for easy access.</li>
                                    <li>The data is extracted into pandas dataframes using SQL read commands.</li>
                                </div>
                            </div>
                        </div>
                        <div ref={this.state.selected === "Browser" ?  this.myRef : null} className={classes.Item}>
                            <h2 onClick={() => this.updateSelected("Browser")}>Web Browser</h2>
                            <div className={this.state.selected === "Browser" ? null : classes.Hide}>
                                <div style={{"marginLeft": "30px"}}>
                                    <li>Built a simple web browser using a Java UI framwork called JavaFX.</li>
                                    <li>It used the WebView library to display the webpage.</li>
                                    <li>Many features of modern browsers were implemented such as history and favorites.</li>
                                </div>
                            </div>
                        </div>
                        {/*<div ref={this.state.selected === "Android" ?  this.myRef : null} className={classes.Item}>
                            <h2 onClick={() => this.updateSelected("Android")}>Android App</h2>
                        </div>*/}
                    </div>
                </Background>
            </div>
        )
    }
}

export default ProjectsPage;
