import React from 'react';
import classes from './Input.module.css';

const input = props => {
    let inputElement = null;
    
    switch ( props.elementType ) {
        case ('input'):
            inputElement = <input
                className={props.red ? [classes.InputElement, classes.Red].join(" ") : classes.InputElement}
                {...props.elementConfig}
                value={props.value}
                onChange={props.changed} />;
            break;
        case ('textarea'):
            inputElement = <textarea
            className={props.red ? [classes.InputElement, classes.Red].join(" ") : classes.InputElement}
                {...props.elementConfig}
                value={props.value}
                onChange={props.changed} />;
            break;
        case ('select'):
            inputElement = (
                <select
                    className={props.red ? [classes.InputElement, classes.Red].join(" ") : classes.InputElement}
                    value={props.value}
                    onChange={props.changed}>
                    {props.elementConfig.options.map(option => (
                        <option key={option} value={option}>
                            {option}
                        </option>
                    ))}
                </select>
            );
            break;
        default:
            inputElement = <input
                className={classes.InputElement}
                {...props.elementConfig}
                value={props.value}
                onChange={props.changed} />;
    }


    return (
        <div className={classes.Input}>
            <label className={classes.Label}><strong>{props.label}</strong></label>
            {inputElement}
        </div>
    );

};

export default input;